@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');

* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

/* css variables throughout the project */
:root {
    --color-bg: #141414;
    --color-bg-variant: #565656;
    --color-primary: #FBA92C;
    --color-primary-variant: #E7E8D1;
    --color-white: #FFFFFF;
    --color-light: #9E9E9E;

    --transition: all 400ms ease; 

    --container-width-lg: 75%; /*large*/
    --container-width-md: 86%; /*medium*/
    --container-width-sm: 90%; /*small*/
}

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    display: none;
}

body {
    font-family: 'Poppins', sans-serif;
    background: var(--color-bg);
    color: var(--color-light);
    line-height: 1.7;
    /* background-image: url(../src/assets/bg-texture.jpg); */
}

/* --------- general styles ---------- */
.container {
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1, h2, h3, h4, h5 {
    font-weight: 500;
}

h1 {
    font-size: 2.5rem;
}

section {
    margin-top: 0rem;
}

section > h2, section > h5 {
    text-align: center;
    color: var(--color-light);
}

section > h2 {
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light {
    color: var(--color-light);
}

a {
    color: var(--color-primary);
    transition: var(--transition);
}

a:hover {
    color: var(--color-white);
}

.btn {
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.08rem 1rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}

.btn:hover {
    background: transparent;
    color: var(--color-white);
    border-color: transparenta;
}

.btn-primary {
    background: var(--color-primary);
    color: var(--color-bg);
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}

.separator {
    width: 100%; 
    height: 17px; 
    border-bottom: 1px solid var(--color-bg-variant); 
    text-align: center;
}

.text {
    /* font-size: 40px; */
    background-color: var(--color-bg);
    padding: 0 10px;
    color: var(--color-primary);
    font-size: 1.5rem;
}


/* tablets media query */
@media screen and (max-width: 1024px) {
    .container{
        width: var(--container-width-md);
    }

    .section {
        margin-top: 2rem;
    }
}

/* phones media query */
@media screen and (max-width: 600px) {
    .container {
        width: var(--container-width-sm);
    }

    .section {
        margin-top: 2rem;
    }

}